	module.exports = {"data":{"markdownRemark":{"html":"<h3>CWE Weakness for Vulnerability Types</h3>\n<p>We've updated our vulnerability taxonomy to include a more complete weakness suite based on the industry-standard Common Weakness Enumeration (CWE). This provide a much more complete and accurate description of a reported vulnerability, and more importantly, it adopts a common language that is endorsed by the security community. </p>\n<h3>Disclosure Assitance with Vulnerability Report</h3>\n<p>HackerOne will now triage and validate disclosure assistance vulnerability reports by severity in order to expedite the disclosure assistance process. </p>","frontmatter":{"path":"/changelog/2017/march","title":"March 2017"}}},"pathContext":{}}

